// pages/PostUpdateForm.jsx

import axios from 'axios';
import React, { useEffect, useRef, useState } from 'react';
import { Button, Container, FloatingLabel, Form } from 'react-bootstrap';
import { useNavigate, useParams } from 'react-router-dom';

function PostUpdateForm(props) {

    // 경로 파라미터 /posts/:id/edit 값을 가지고 있는 object 얻어내기
    // {id: '수정할 글번호'} 형식의 object가 리턴된다. 따라서 수정할 번호는 params.id 이다.
    const params = useParams();

    const [post, setPost] = useState({});

    let savedPost = useRef(null); // savedPost 는 Object 이고 current 라는 방에 저장된 값이 들어 있다.

    useEffect(()=>{
        // 컴포넌트가 활성화되는 시점에 수정할 글의 번호를 이용해서 수정할 글의 정보를 로딩
        axios.get(`/v3/posts/${params.id}`)
        .then(res=>{
            setPost(res.data);
            // useRef()가 리턴한 object 의 current 에 초기 post를 저장해둔다.
            savedPost.current = res.data;
        })
        .catch(error=>console.log(error));
    },[])

    // title or author 입력란에 change 이벤트가 발생했을 때 상태값을 변경하도록 한다.
    const handleChange = (e) => {
        setPost({
            ...post,
            [e.target.name]:e.target.value
        })
    };

    const navigate = useNavigate();

    const update = () => {
      axios.put(`/v3/posts/${post.id}`, post)
                .then(res=>{
                    alert("수정 완료!");
                    navigate("/posts");
                })
                .catch(error=>console.log(error));
    };
    const cancel = () => {
      setPost(savedPost.current);
    };

    return (
        <>
          <h1>{post.id}번 글 수정페이지</h1>
          <Container>
            <FloatingLabel label="글 번호" className='mb-3' controlId='id'>
              <Form.Control type="text" name="id" value={post.id} readOnly />
            </FloatingLabel>
            <FloatingLabel label="제목" className='mb-3' controlId='title'>
              <Form.Control type="text" name="title" value={post.title} placeholder='제목 입력...' onChange={handleChange} />
            </FloatingLabel>
            <FloatingLabel label="작성자" className='mb-3' controlId='author'>
              <Form.Control type="text" name="author" value={post.author} placeholder='작성자 입력...'onChange={handleChange} />
            </FloatingLabel>
            <Button onClick={update} className='btn btn-success me-3'>저장</Button>
            <Button onClick={cancel} className='btn btn-danger'>취소</Button>
          </Container>
        </>
    );
}

export default PostUpdateForm;