import { NavLink, useNavigate, useOutlet } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css"; // Bootstrap 추가
import { Container, Nav, Navbar } from "react-bootstrap"; // Bootstrap 컴포넌트 추가

function App() {
    // 현재 route 된 정보를 출력해주는 hook
    const currentOutlet = useOutlet();

    // javascript 로 route 이동을 하게 해주는 Hook
    //const navigate = useNavigate();

    return (
        <>
            <Navbar expand="md" className="bg-warning">
                <Container>
                    <Navbar.Toggle aria-controls="one" />
                    <Navbar.Brand as={NavLink}>Acorn</Navbar.Brand>
                    <Navbar.Collapse>
                        <Nav>
                            <Nav.Link as={NavLink} to="/">Home</Nav.Link>
                            <Nav.Link as={NavLink} to="/game">Game</Nav.Link>
                            <Nav.Link as={NavLink} to="/study">Study</Nav.Link>
                            <Nav.Link as={NavLink} to="/posts">Post</Nav.Link>
                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>
            <Container className="mt-3">
                {/* 네비게이션 바 */}
                <Nav className="justify-content-center mb-4">
                    <Nav.Item>
                        <NavLink to="/" className="btn btn-primary mx-2">Home</NavLink>
                    </Nav.Item>
                    <Nav.Item>
                        <NavLink to="/game" className="btn btn-success mx-2">Game</NavLink>
                    </Nav.Item>
                    <Nav.Item>
                        <NavLink to="/study" className="btn btn-warning mx-2">Study</NavLink>
                    </Nav.Item>
                    <Nav.Item>
                        <NavLink to="/posts" className="btn btn-danger mx-2">Post</NavLink>
                    </Nav.Item>
                    <Nav.Item>
                        <NavLink to="/posts?pageNum=2" className="btn btn-danger mx-2">Post 2페이지로 가기</NavLink>
                    </Nav.Item>
                </Nav>

                {/* <button className="btn btn-primary" onClick={()=>{
                // javascript 로 "/post" 경로로 라우트 되게 하려면?
                navigate("/posts");
            }}>Post</button>
             <button className="btn btn-primary" onClick={()=>{
                // javascript 로 "/post" 경로로 라우트 되게 하려면?
                navigate("/posts?pageNum=2");
            }}>Post 2page</button> */}

                {/* 현재 선택된 라우트 컴포넌트 */}
                <div>{currentOutlet}</div>
            </Container>
        </>
    );
}

export default App;